import HomeCloud from '@/assets/imgs/icon/cloud.png';
import HomeDataCenter from '@/assets/imgs/icon/datacentre.png';
import HomeSoftware from '@/assets/imgs/icon/software.png';
import HomeUnion from '@/assets/imgs/icon/union.svg';
import HomeWeb from '@/assets/imgs/icon/web.png';
import WebUnion from '@/assets/imgs/icon/wunion.svg';
import ContentWrap from '@/components/content-wrap';
import Title from '@/components/title';
import { Link } from 'react-router-dom';
import './index.scss';

const SolutionData = [
  {
    img: HomeDataCenter,
    imgAlt: '',
    title: 'Data Center Service',
    content:
      'Through us, you can enjoy world-class architecture and facility management, with quality hosting service at a competitive price.',
    link: '/data'
  },
  {
    img: HomeCloud,
    imgAlt: '',
    title: 'Cloud Hosting',
    content:
      'Our team’s vast knowledge and experience allows us to support your business goals, be it adopting Cloud Computing, CRM system or VoIP.',
    link: '/cloud'
  },
  {
    img: HomeSoftware,
    imgAlt: '',
    title: 'Software Service',
    content:
      'We have a great deal of experience in designing applications and software to improve your business process while meeting business goals and objects.',
    link: '/software'
  },
  {
    img: HomeWeb,
    imgAlt: '',
    title: 'Web Development',
    content:
      'We provide both client-side and server-side coding services. We also initiate the coding phase of development, making our developers & teams work to the highest qualified.',
    link: '/web'
  }
];

const Solution = ({ isMobile }) => {
  return (
    <div>
      <div className="solutionPinkbg">
        <div
          className="font-42 white-c"
          style={{ textAlign: 'center', paddingTop: '95px' }}
        >
          Need help with your next project?
        </div>
        <div
          className="font-16 white-c"
          style={{
            width: 'fit-content',
            margin: 'auto',
            paddingTop: '36px',
            cursor: 'pointer'
          }}
        >
          <span
            style={{ borderBottom: '1px solid white' }}
            // onClick={() => (window.location.href = '/contact')}
          >
            <a href="/contact">Contact our sales team</a>
          </span>
          <img style={{ paddingLeft: 5 }} src={WebUnion} alt="" />
        </div>
      </div>
      <ContentWrap>
        <div style={{ marginTop: 100 }}></div>
        <Title title="Our Solutions" />
        <div className="home-content-2">
          {SolutionData.map((content, index) => (
            <div key={index}>
              <div className="img-wrap">
                <img src={content.img} alt={content.imgAlt} />
              </div>
              <div className="font-20 font-bold">{content.title}</div>
              <div className="font-16 grey-color">{content.content}</div>
              <Link
                className="font-16 font-bold"
                to={content.link}
                onClick={() => {
                  window.scroll(0, 0);
                }}
              >
                <span
                  style={{
                    color: '#ec407a',
                    marginRight: 8,
                    borderBottom: '2px solid #ec407a',
                    paddingBottom: 2
                  }}
                >
                  Learn More
                </span>
                <img src={HomeUnion} alt="" />
              </Link>
            </div>
          ))}
        </div>
      </ContentWrap>
      {/* <ContentWrap
        paddingTop="75px"
        paddingBottom="125px"
        height="1950px"
        background="#fafafa"
      >
        <div
          className="software-service"
          style={{ width: 950, margin: 'auto', position: 'relative' }}
        >
          <Title
            title="Software service"
            width="840px"
            content={[
              'Our Hong Kong-based software development team offers a diverse range of solutions for a horizontal tier of industries. We had a sophisticated on-site and off-site model that is on par with global well-accepted standards.',
              'We present solutions that are personal and productive. All our solutions are customizable and can be crafted to suit your business. Over the years, Coffee Cloud has offered a range of solutions that are proven to be very successful.'
            ]}
          />
        </div>
      </ContentWrap> */}
    </div>
  );
};

export default Solution;
